\n\n.navbar-nav {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  padding: 0.5rem 3rem;\n}\n\n.logo {\n  width: 5rem;\n  height: auto;\n  padding: 0.8rem;\n}\n\n.nav-list {\n  list-style: none;\n  display: flex;\n  gap: 2rem;\n}\n\n.nav-item a {\n  font-size: 20px;\n  text-decoration: none;\n  padding: 0.5rem ;\n  color: var(--clr-primary);\n  cursor: pointer;\n  transition: all .3s ease-in-out;\n}\n\n.nav-item a:hover {\n  border-bottom: 4px solid var(--clr-solid);\n}\n\n// Warna latar belakang kuning saat discroll\n.scroll-background {\n  background-color: var(--clr-brush);\n}\n\n// ini hamburger button\n.header-menu {\n  display: inline-block;\n  padding: 5px;\n  min-width: 50px;\n  min-height: 50px;\n  font-size: 32px;\n  text-decoration: none;\n  color: var(--clr-primary);\n}\n\n.centered {\n  display: block;\n  text-align: center;\n}\n\n/* Hero Jumbotron */\n.hero {\n  display: flex;\n  align-items: center;\n  min-height: 100vh;\n  width: 100%;\n  text-align: center;\n  background: linear-gradient(0deg, rgba(0, 0, 0, 0.20) 0%, rgba(0, 0, 0, 0.40) 100%), url('../public/images/heros/hero-image_2.jpg'), lightgray 50% / cover no-repeat;\n  background-size: cover;\n  background-position: center;\n  background-color: var(--clr-primary);\n}\n\n.hero-inner {\n  margin: 2rem auto;\n  max-width: 600px;\n}\n\n.hero-title {\n  color: var(--clr-primary);\n  text-shadow: 0 6px 6px rgba(0, 0, 0, 0.5);\n  font-weight: 800;\n  font-size: 36px;\n}\n\n.hero-tagline {\n  color: var(--clr-primary);\n  text-shadow: 0 6px 6px rgba(0, 0, 0, 0.5);\n  margin-top: 16px;\n  font-size: 18px;\n  font-weight: 700;\n}\n\n\n// Main Section \n.custom-padding-h1 {\n  padding-top: 4rem;\n  text-align: center;\n  color: var(--clr-primary);\n}\n\n\n.container{\n  position: relative;\n  display: flex;\n  justify-content: center;\n  align-items: center; \n  flex-wrap: wrap;\n  gap: 80px 50px;\n  padding: 100px 50px;\n  min-height: 100vh;\n}\n\n.container .card {\n  po